/* Map styling */
.app {
    border: 5px solid purple;
    display: -ms-flexbox;
    display: flex;
}
  
.map {
    display: -ms-flexbox;
    display: flex;
}

.address-form {
    margin-top: 5%;
    margin-bottom: 5%;
    text-align: center;
    width: 100%;
}

.address-form > label, .address-form > input{
    margin-right: 2%;
}

#right-panel {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

#hotelbtn {
    background:#FF5A5F;
    border-radius: 5px;
	color:white;
}

.legend { 
    border: 2px solid black;
    text-align: center;
    padding-top: 3%;
    margin-top: 10%;
}

#driving {
    color: blue;
}

#transit {
    color: orange;
}

#walking {
    color: purple;
}

#bicycling {
    color: #9ACD32;
}

/* 'Get Directions' button */
#controlUI {
    background-color: #fff;
    border: 2px solid #fff;
    border-radius: 3px;
    -webkit-box-shadow: 0 2px 6px rgba(0,0,0,.3);
            box-shadow: 0 2px 6px rgba(0,0,0,.3);
    margin-bottom: 22px;
    text-align: center;
}

#controlText {
    color: rgb(25, 25, 25);
    font-size: 16px;
    line-height: 38px;
    padding-left: 5px;
    padding-right: 5px;
}

/* Infowindow styling */
#content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    overflow: hidden;
}

#content h2 {
    margin-top: 1%;
    font-size: 2vw;
}

#content p {
    font-size: 1.2vw;
}

#place_image {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin-top: 2%;
    margin-bottom: -4%;
}

#place_image > img {
    width: 15vh;
	height: 15vh;
    margin: 0 auto;
}

#infowindow-controls {
    display: -ms-flexbox;
    display: flex;
}

#btn-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

#btn-container > button {
    background:#FF5A5F;
	border-top: 1px solid #FF5A5F;
	border-bottom: 1px solid #FF5A5F;
    color:white;
    border-radius: 5px;
    width: 4.5vw;
    height: 4.9vh;
    font-size: 0.9vw;
}

#travel-mode {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    width: 100%;
}

#mode-description {
    margin-right: 2%;
    display: -ms-flexbox;
    display: flex;
}

#mode-description > p {
    height: 100%;
    font-size: 1.2vw;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

#mode-type {
    display: -ms-flexbox;
    display: flex;
}



